import Layout from '../components/layout';

const Projects = () => (

    <div>
        <Layout>
            <section className="content">
                <div className="row">
                    <div className="offset-lg-3 col-lg-6">
                        <div className="paper">
                            <h1 className="written text-center">my work.</h1>
                            <p>
                                Below are a series of projects that I've either fully developed or contributed to the development of.
                            </p>
                        </div>
                    </div>
                </div>
                <hr></hr>
                {/* Sierra Management Group */}
                <div className="row">
                    <div className="col-12">
                        <h2 className="text-center">Sierra Management Group</h2>
                    </div>
                </div>
                <div className="row smg">
                    <div className="col-md-6">
                        <img className="img-fluid" src="assets/images/smg.png"></img>
                    </div>
                    <div className="col-md-6">
                        <h2><a href="https://sierramanagementgroup.com/">www.sierramanagementgroup.com</a></h2>
                        <h3>developed while working at LBC IT Solutions Inc.</h3>
                        <ul>
                            <li></li>
                            <li><a href="https://sierramanagementgroup.com/">www.sierramanagementgroup.com</a></li>
                            <li>fully developed using WordPress with an isotope.js theme.</li>
                        </ul>
                    </div>
                </div>
            </section>
        </Layout>
        <style jsx>{`
            .smg {
                background-color: #242423;
                color: #fff;
            }

            a {
                text-decoration: none;
                color: inherit;
            }

            a:hover {
                border-bottom: 1.5px solid;
            }
        `}</style>
    </div>
);

export default Projects;